html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

i {
  color: white;
}

* {
  outline: none !important;
}

a {
  text-decoration: none;
  color: #fff;
  display: block;
}

button {
  border-style: none;
}

input {
  text-decoration: none;
  border-style: none;
  background: none;
}

.container, .header {
  padding: 0 200px;
}

@media (max-width: 1366px) {
  .container, .header {
    padding: 0 calc(50% - 470px);
  }
}

@media (max-width: 991px) {
  .container, .header {
    padding: 0 40px;
    margin: 0 auto;
  }
}

@media (max-width: 767px) {
  .container, .header {
    padding: 0 20px;
    margin: 0 auto;
  }
}

.btn-base {
  background: #FFFFFF;
  box-shadow: 0px 8px 20px rgba(224, 224, 224, 0.5);
  border-radius: 100px;
  margin: 0 auto;
  color: #57687F;
  font-family: "Nunito", sans-serif;
  font-size: 14px;
  line-height: 19px;
}

.btn-base:hover {
  opacity: .7;
  transition: all .2s ease;
  cursor: pointer;
  box-shadow: 20px 20px 20px rgba(224, 224, 224, 0.5);
}

.header {
  height: 100vh;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url("/css/img/bg2.jpeg");
  background-size: cover;
  background-position: top;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0 auto;
  padding-bottom: 80px;
  padding-top: 25px;
  box-sizing: border-box;
}

@media (max-width: 767px) {
  .header {
    width: 100%;
    height: 100%;
  }
}

.header::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #272727;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  z-index: -2;
}

.nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.logo:hover {
  cursor: pointer;
  opacity: .8;
  transition: .2s;
}

.menu {
  margin-left: 200px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.menu__btn {
  border: 1px solid #FFFFFF;
  box-sizing: border-box;
  border-radius: 100px;
  padding: 8px 26px;
}

.menu__item {
  display: block;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  line-height: 16px;
  color: white;
  text-decoration: none;
  margin-left: 70px;
  cursor: pointer;
}

.menu__item:hover {
  transition: all 1s ease;
}

@font-face {
  font-family: "Font Awesome 5 Free";
  src: url("/css/fonts/Font Awesome5Free-Solid-900.otf");
}

.hamburger-button {
  display: none;
  position: relative;
}

.hamburger-button--active::before {
  font-family: "Font Awesome 5 Free";
  display: inline-block;
  content: "\f00d";
  font-weight: 900;
  color: black;
  font-size: 26px;
}

.hamburger-button::before {
  font-family: "Font Awesome 5 Free";
  display: inline-block;
  content: "\f0c9";
  font-weight: 900;
  color: white;
  font-size: 22px;
}

@media (max-width: 991px) {
  .menu {
    margin-left: 200px;
  }
  .menu__item {
    margin-left: 40px;
  }
}

@media (max-width: 767px) {
  .hamburger-button {
    display: block;
  }
  .menu {
    margin-left: auto;
    margin-right: 0;
  }
  .menu__item:not(:first-child) {
    display: none;
  }
  .menu.responsive {
    position: relative;
    margin-left: 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: #fff;
    height: 70vh;
  }
  .menu.responsive .menu__item {
    margin: 15x auto;
    color: black;
    display: block;
    font-size: 22px;
  }
  .menu.responsive .menu__item > a, span::before {
    color: black;
  }
}

